<template>
  <div class="login">
    <div class="login_box">
      <div class="login_top_nav">
        <img src="~@/assets/imgs/mainlogo.jpg" alt="" />
        <img src="~@/assets/imgs/productKSOA.jpg" alt="" />
        <span>v9.0</span>
      </div>
      <div class="login_select_options">
        <el-button type="info" size="small" :autofocus="true"
          >企业门户</el-button
        >
        <el-button type="info" size="small">供应商协同</el-button>
        <el-button type="info" size="small">客户协同</el-button>
      </div>
      <div class="login_form">
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="80px"
          size="small"
        >
          <el-form-item label="子系统">
            <el-select v-model="ruleForm.region" placeholder="供应链管理系统">
              <el-option label="供应链管理系统" value="supplyChain"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="用户名" prop="username">
            <el-input v-model="ruleForm.username" clearable></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input
              v-model="ruleForm.password"
              clearable
              type="password"
            ></el-input>
          </el-form-item>
          <el-form-item label="校验码" prop="verify">
            <div class="verify_code">
              <el-input
                v-model="ruleForm.verify"
                clearable
                style="margin-right: 5px"
              ></el-input>
              <div ref="svg" style="cursor: pointer">
                <img src="~@/assets/imgs/com.sksoft.v8.cb.jpg" alt="" />
              </div>
            </div>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"
              >登录</el-button
            >
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { checkName, checkPass, validateCaptcha } from "../config/formRules.js";
import { mapState, mapMutations } from "vuex";
export default {
  name: "Login",
  components: {},
  data() {
    var validateCaptcha = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("验证码不能为空"));
      } else if (value.toLowerCase() != "7646") {
        callback(new Error("验证错误"));
      } else {
        callback();
      }
    };
    return {
      userInfo: {},
      svgBol: "", //验证码
      // 表单数据
      ruleForm: {
        username: "",
        password: "",
        region: "",
        verify: "",
      },
      // 验证规则
      rules: {
        username: [{ required: true, validator: checkName, trigger: "blur" }],
        password: [{ required: true, validator: checkPass, trigger: "blur" }],
        verify: [
          { required: true, message: "验证码不能为空", trigger: "blur" },
          { validator: validateCaptcha, trigger: "blur" },
        ],
      },
    };
  },
  computed: {
    ...mapState(["username", "password"]),
  },
  methods: {
    ...mapMutations(["getUsername", "getPassword", "getdzyname"]),
    // 提交表单
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const { data: res } = await this.$http.post("/catalogue/userlogin", {
            lgnname: this.ruleForm.username,
            kl: this.ruleForm.password,
          });
          if (res.code != 200) {
            this.$message.error(res.value);
            return;
          }
          this.userInfo = res.data[0];
          // console.log(res, "userinfo");
          if (this.userInfo) {
            this.getdzyname(this.userInfo.dzyname);
            localStorage.setItem("dzyname", this.userInfo.dzyname);
          }
          localStorage.setItem("username", this.ruleForm.username);
          localStorage.setItem("password", this.ruleForm.password);
          this.getUsername(this.ruleForm.username);
          this.getPassword(this.ruleForm.password);
          this.$router.push("/Main");
        } else {
          return false;
        }
      });
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 自动填充密码
    reStart() {
      const username = localStorage.getItem("username");
      const password = localStorage.getItem("password");
      if (username) {
        this.ruleForm.username = this.username;
      }
      if (password) {
        this.ruleForm.password = this.password;
      }
    },
    // async getVerifycode() {
    //   const { data: res } = await this.$http.get("/catalogue/getVerifycode");
    //   this.$refs.svg.innerHTML = res.data.data;
    //   this.svgBol = res.data.text.toLowerCase();
    // },
  },
  created() {
    this.reStart();
    // this.getVerifycode();
  },
};
</script>
