import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    username: '',
    password: '',
    dzyname: ''
  },
  mutations: {
    getUsername(state, username) {
      state.username = username
    },
    getPassword(state, password) {
      state.password = password
    },
    getdzyname(state, dzyname) {
      state.dzyname = dzyname
    }
  },
  actions: {
    getSession({
      commit
    }) {
      const user = localStorage.getItem('username')
      const pass = localStorage.getItem('password')
      const dzyname = localStorage.getItem('dzyname')
      if (user) {
        commit('getUsername', user)
      }
      if (pass) {
        commit('getPassword', pass)
      }
      if (dzyname) {
        commit('getdzyname', dzyname)
      }
    }
  },
  modules: {}
})