import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: "/404",
    name: 'notfound',
    component: () => import('../views/Notfound.vue')
  },
  {
    path: '*',
    redirect: "/404"
  },
  {
    path: '/main',
    redirect: '/DataCenter',
    component: function () {
      return import('../views/Main.vue')
    },
    children: [{
        path: '/DataCenter',
        component: () => import('../components/RightNav/DataCenter.vue')
      },
      {
        path: '/ServiceDataQuery',
        name: 'ServiceDataQuery',
        component: () => import('../components/RightNav/ServiceDataQuery.vue')
      }
    ]
  }
]

// 重写push和replace方法 解决多次点击报错问题
const VueRouterPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(to) {
  return VueRouterPush.call(this, to).catch(err => err)
}

const VueRouterReplace = VueRouter.prototype.replace
VueRouter.prototype.replace = function replace(to) {
  return VueRouterReplace.call(this, to).catch(err => err)
}



const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 前置导航守卫
router.beforeEach((to, from, next) => {
  const username = localStorage.getItem('username')
  if (to.path !== '/') {
    if (username) {
      next()
    } else {
      next('/')
    }
  } else {
    next()
  }

})

export default router